<script>
	import Vue from 'vue'
	export default {
		name:'article',
		data(){
			return {
				msg:'hello world',
				maincon:'',
				artid:'',
				topdir:'',
				title:'',
				likart:'',
				pagestart:null,
      			pageend:null
			}
		}, 
		created () {
		  
		},
		 watch: {
		   '$route' (to, from) {
		     //banner lunbo
			 	$(".list-anli .flexslider").flexslider({
			    	animation : "slide"
			    });
			    
			   this.artid = (this.$route.path).substring(5);
			   
			   let _this =this;
			   let nid =this.artid;
			   //
			   _this.$data.pagestart = (new Date()).getTime();
			   //loading
  			 	let loading = Vue.prototype.$loading({text:"...",background:"#80bd01",spinner:'el-icon-loading'});
			   
			    //
			    $.ajax({
					type : "POST",
				    url : "http://m.0832pifu.com/test/test1.php",
				    data:{'artid':nid},
				    dataType : "jsonp",
				    jsonp: "callback",//jsonp(:callback)
				    jsonpCallback:"success_jsonpCallback",//jsonpjQuery
				    success : function(res){
				    	let str = res[0].body;
				    	//str.replace
				    	res[0].body = str.replace(/\/uploads/g,"http://m.0832pifu.com/uploads");
				    	//maincon
						_this.$data.maincon =res[0];
						//
						$.each(diseaseList, function(idx,obj) {
							if(res[0].typename==obj.typename){
								_this.topdir =obj.typedir;
							}
						});	
						//
						let a =_this.maincon.typename;
						//
						let b =JSON.parse(storage.getItem(a));
						let c;
							_this.$data.likart =b;
						$.each(b, function(idx,obj) {
							if(_this.$route.path == obj.id){
								_this.$data.title = obj.title;
								c =obj;
							}
						});
						for(let i=0; i<b.length; i++) {
					    if(b[i] == c) {
						      b.splice(i, 1);
						      break;
						    }
						  }
						
						_this.$data.likart =b;
						
						_this.$data.pageend =(new Date()).getTime();
						let loadingtime = (_this.$data.pageend -_this.$data.pagestart)>1000 ? 0:500;
						
						setTimeout((function(){loading.close()}),loadingtime);
					
				    },
				    error:function(){
				        alert('fail');
				    }
				});
				
				 $('body,html').animate({ scrollTop: 0 }, 0);
		     
		   }
		 },
		 
		mounted(){
			//banner lunbo
		 	$(".list-anli .flexslider").flexslider({
		    	animation : "slide"
		    });
		    
		   this.artid = (this.$route.path).substring(5);
		   
		   let _this =this;
		   let nid =this.artid;
	      //
		   _this.$data.pagestart = (new Date()).getTime();
		   //loading
		 	let loading = Vue.prototype.$loading({text:"...",background:"#80bd01",spinner:'el-icon-loading'});
		  
		    //
		    $.ajax({
				type : "POST",
			    url : "http://m.0832pifu.com/test/test1.php",
			    data:{'artid':nid},
			    dataType : "jsonp",
			    jsonp: "callback",//jsonp(:callback)
			    jsonpCallback:"success_jsonpCallback",//jsonpjQuery
			    success : function(res){
			    	let str = res[0].body;
			    	//str.replace
			    	res[0].body = str.replace(/\/uploads/g,"http://m.0832pifu.com/uploads");
			    	
					_this.$data.maincon =res[0];
					$.each(diseaseList, function(idx,obj) {
						if(res[0].typename==obj.typename){
							_this.topdir =obj.typedir;
						}
					});	
					let a =_this.maincon.typename;
					let b =JSON.parse(storage.getItem(a));
					let c;
						
					$.each(b, function(idx,obj) {
						
						if(obj.id ==_this.$route.path ){
							_this.$data.title = obj.title;
							c =obj;
						}
					});
					 for(let i=0; i<b.length; i++) {
					    if(b[i] == c) {
					      b.splice(i, 1);
					      break;
					    }
					  }
					 
					_this.$data.likart =b;
					
						_this.$data.pageend =(new Date()).getTime();
						let loadingtime = (_this.$data.pageend -_this.$data.pagestart)>1000 ? 0:500;
						
						setTimeout((function(){loading.close()}),loadingtime);
					
					
					
			    },
			    error:function(){
			        alert('fail');
			    }
			});
			 $('body,html').animate({ scrollTop: 0 }, 0);
		}
	}
</script>

