<script>
import Vue from 'vue'

export default {
  name: 'list',
  data () {
    return {
      bigPage:false,
      name:'',
      nameid:'',
      diseases:'',
      totallist:20,
      currentPage:1,
      pagesize:6,
      pagestart:null,
      pageend:null
    }
  },
  mounted (){
  	$('body,html').animate({ scrollTop: 0 }, 0);
  	let path = this.$route.path;
  		
  	let _this =this;
  	 	$.each(diseaseList, function(index,value) {
  		if(value.typedir == path){
  			_this.$data.name =value.typename;
  			_this.$data.nameid = value.id;
  		}
  	 });
  	 var nid = this.nameid;
  	 _this.$data.pagestart = (new Date()).getTime();
  	 if (!storage.getItem(this.name)) {
  	 	
  		 $(".sec4").hide();
  	 	let loading = Vue.prototype.$loading({text:"...",background:"#80bd01",spinner:'el-icon-loading'});
  	 	
	  	 	$.ajax({
			type : "POST",
		    url : "http://m.0832pifu.com/test/test1.php",
		    data:{'typeid':nid},
		    dataType : "jsonp",
		    jsonp: "callback",//jsonp(:callback)
		    jsonpCallback:"success_jsonpCallback",//jsonpjQuery
		    success : function(res){
				$.each(res, function(idx,obj) {
					//
					obj.litpic ='http://m.0832pifu.com'+obj.litpic;
					//
					obj.description= obj.description.substr(0,20);
					obj.id ='/foo/'+obj.id;
				});
				_this.$data.diseases =res;
				_this.$data.totallist =res.length;
				var b = JSON.stringify(_this.$data.diseases);
				
				storage.setItem(_this.$data.name,b);
				_this.$data.pageend =(new Date()).getTime();
				let loadingtime = (_this.$data.pageend -_this.$data.pagestart)>1000 ? 0:500;
				
				setTimeout((function(){loading.close()
						$(".sec4").show();
				}),loadingtime);
		    },
		    error:function(){
		        alert('fail');
		    }
		});
  	 }else {
  		let loading = Vue.prototype.$loading({text:"...",background:"#80bd01",spinner:'el-icon-loading'});
  	 		//loading
  	 		$(".sec4").hide();
  	 		_this.$data.diseases =JSON.parse(storage.getItem(_this.$data.name));//
			
  	 		_this.$data.totallist = _this.$data.diseases.length;
  	 	_this.$data.pageend =(new Date()).getTime();
		let loadingtime = (_this.$data.pageend -_this.$data.pagestart)>1000 ? 0:500;
		
		setTimeout((function(){loading.close();
		
  	 		$(".sec4").show();
  	 		
		}),loadingtime);
  	 }
  	 if(this.totallist>40){
  	 	this.bigPage=true;
  	 }
 //banner lunbo
