    	animation : "slide"
    });
    /**/
	$(".tab>ul>li").each(function(i){
		$(this).click(function(){
			$(".tab ul li").css({'color':"white",'background':'#55b0ff',"fontWeight":'normal'});
			$(this).css({'color':"#55b0ff",'background':'#e6f3ff',"fontWeight":'bold'})
			$(".tab-item").css("display",'none');
