  	let that = this;
  	window.onload = () => { this.data = diseaseList 
  		
  		/*let timer =	setInterval((function(){
  			that.percentage ++;
  			if(that.percentage == 100){
  				clearInterval(timer);
  				that.success = 'success'
  			}
  		}),100);*/
  		
  	}
  },
  mounted (){
  	this.data =diseaseList;
  
 //banner lunbo
 	$(".banner_2 .flexslider").flexslider({
    	animation : "slide"
    	
    });
  },
  methods:{
  	open:function(){
  		var re= /select|update|delete|exec|count|'|"|=|;|>|<|%/i;
		var name =$.trim($("input[name='name']").val()),
			call =$.trim($("input[name='call']").val()),
			datenow =$.trim($("input[name='datenow']").val()),
			discrib =$.trim($("input[name='discrib']").val());
			
			if(re.test(name)||re.test(call)||re.test(datenow)||re.test(discrib)){
				this.$message({
					 	type: 'warning',
      				      message: '!'
					 });
				return false;
			}else {
				if(name.length ==0){
					 this.$message({
					 	type: 'error',
      				      message: ' !'
					 });
					return false;
				}else if(name.length>10||!name.match(/^[\u4e00-\u9fa5]+$/)){
					this.$message({
					 	type: 'error',
      				      message: '!'
					 });
					$("input[name='name']").val("");
					return false;
				}else {
					if(call.length==0){
						this.$message({
					 	type: 'error',
      				      message: '!'
					 });
						return false;
					}else if(!call.match(/^1[34578]\d{9}$/)){
						this.$message({
					 	type: 'error',
      				      message: '!'
					 });
						return false;
					}else {
						if(discrib.length==0){
							this.$message({
							 	type: 'error',
		      				      message: '!'
							 });
							return false;
						}else {
							$("form").submit();
						}
							
					}
				
			}
		}
  	}
  }
}
</script>

